    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 100px;
    
    
}

.home-mid-text h1{
    font-size: 3.75rem;
}
.home-mid-text p{
    font-size: 20px;
    font-weight: 800;
    max-width: 40ch;
}

.home-mid-text button{
    width: 10rem;
    height: 3rem;
    border-radius: 10px;
    border: none;
    outline: none;
    background-color: #4654A3;
    color:#fff;
    margin-top: 40px;
    cursor: pointer;  
}


.help-container{
    width: 100%;
    margin: 20px;
}
